* {
  box-sizing: border-box;
}

#root {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100vh;
}

.app {
  text-align: center;
  padding: 1rem;
  height: 100%;
  background: linear-gradient(180deg, #e3ffe7 0%, #d9e7ff 100%);
  color: #111;
}
.title {
  margin-bottom: 2rem;
}

.explanations {
  text-align: left;
}
.form {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin-top: 2rem;
}
.input {
  height: 3rem;
  font-size: 2rem;
  text-align: center;
}
.button {
  width: fit-content;
  display: flex;
  flex-direction: column;
  margin: auto;
  font-size: 1.5rem;
  padding: 1rem;
  border-radius: 0.5rem;
}
